import VisualComponent from "jx/core/comps/VisualComponent";
/**
 * Implements a simple image.
 * @example
 *
 * // json
 * {
 * 	    "id": "ImageID",
 * 	    "type": "Image",
 * 	    "preload": true,
 * 	    "asset": {
 * 	    "src": "images/ImageID.png"
 * 	    }
 * },
 *
 * // code
 * var myImage = this.jx.cc({rid: "ImageID"});
 */
export default class Image extends VisualComponent {
    /**
    * @param {object} dataObject
    */
    constructor(dataObject) {
        super(dataObject);
        this.extendsFrom("Image");
    }
    /**
     * Change ByteArray Data of image.
     * @param  {array} bitmapData ByteArray
     */
    set bitmapData(bitmapData) {
        this.view.bitmapData = bitmapData;
    }
}
